.svgMap-map-wrapper .svgMap-country {
    stroke: #fff;
    stroke-width: 1;
    stroke-linejoin: round;
    vector-effect: non-scaling-stroke;
    transition: fill .25s, stroke .25s
}

.svgMap-map-wrapper .svgMap-country[data-link] {
    cursor: pointer
}

@media(hover:hover) {
    .svgMap-map-wrapper .svgMap-country:hover {
        stroke: #333;
        stroke-width: 1.5
    }
}

.svgMap-map-wrapper .svgMap-country.svgMap-active {
    stroke: #333;
    stroke-width: 1.5
}

.svgMap-tooltip {
    background: #fff;
    border-bottom: 1px solid #000;
    border-radius: 2px;
    box-shadow: 0 0 3px rgba(0, 0, 0, .2);
    display: none;
    min-width: 60px;
    pointer-events: none;
    position: absolute;
    transform: translate(-50%, -100%);
    z-index: 2
}

.svgMap-tooltip.svgMap-tooltip-flipped {
    border-bottom: 0;
    border-top: 1px solid #000;
    transform: translate(-50%)
}

.svgMap-tooltip.svgMap-active {
    display: block
}

.svgMap-tooltip .svgMap-tooltip-content-container {
    padding: 10px 20px;
    position: relative
}

.svgMap-tooltip .svgMap-tooltip-content-container .svgMap-tooltip-flag-container {
    margin: 2px 0 5px;
    text-align: center
}

.svgMap-tooltip .svgMap-tooltip-content-container .svgMap-tooltip-flag-container.svgMap-tooltip-flag-container-emoji {
    font-size: 50px;
    line-height: 0;
    padding: 25px 0 15px
}

.svgMap-tooltip .svgMap-tooltip-content-container .svgMap-tooltip-flag-container .svgMap-tooltip-flag {
    background: rgba(0, 0, 0, .15);
    border-radius: 2px;
    display: block;
    height: 32px;
    margin: auto;
    padding: 2px;
    width: auto
}

.svgMap-tooltip .svgMap-tooltip-title {
    font-size: 18px;
    line-height: 28px;
    padding: 0 0 8px;
    text-align: center;
    white-space: nowrap
}

.svgMap-tooltip .svgMap-tooltip-content {
    color: #777;
    font-size: 14px;
    margin: -5px 0 0;
    text-align: center;
    white-space: nowrap
}

.svgMap-tooltip .svgMap-tooltip-content table {
    border-spacing: 0;
    margin: auto;
    padding: 0
}

.svgMap-tooltip .svgMap-tooltip-content table td {
    padding: 2px 0;
    text-align: left
}

.svgMap-tooltip .svgMap-tooltip-content table td span {
    color: #111
}

.svgMap-tooltip .svgMap-tooltip-content table td:first-child {
    padding-right: 10px;
    text-align: right
}

.svgMap-tooltip .svgMap-tooltip-content table td sup {
    position: relative;
    top: -5px;
    vertical-align: baseline
}

.svgMap-tooltip .svgMap-tooltip-content .svgMap-tooltip-no-data {
    color: #777;
    font-style: italic;
    padding: 2px 0
}

.svgMap-tooltip .svgMap-tooltip-pointer {
    height: 10px;
    left: 50%;
    overflow: hidden;
    position: absolute;
    top: 100%;
    transform: translateX(-50%);
    width: 30px
}

.svgMap-tooltip .svgMap-tooltip-pointer:after {
    background: #fff;
    border: 1px solid #000;
    bottom: 6px;
    content: "";
    height: 20px;
    left: 50%;
    position: absolute;
    transform: translateX(-50%) rotate(45deg);
    width: 20px
}

.svgMap-tooltip.svgMap-tooltip-flipped .svgMap-tooltip-pointer {
    bottom: auto;
    top: -10px;
    transform: translateX(-50%) scaleY(-1)
}